









































export default {
	data() {
		return {
			dc_info: '',
			account: '',
			// 支付状态现金 或 积分抵扣
			status: '0',
			// 是否选中积分抵扣
			isAcc: false,
			// 支付方式
			mode: 0,
			money: 0,
			// 最后价格
			price: 0,
			dc_id: '',

		};
	},
	onShow(){
		console.log(this.isAcc)
	},
	onLoad(option) {
		// console.log(option.id)
		// 请求电池详情
		var that = this;
		// 存电池id
		// that.$store.state.dcId = option.id
		uni.setStorageSync('dcId',option.id)
		that.dc_id = option.id;
		uni.showLoading({
			title: '加载中...'
		})
		uni.request({
			url: that.http + 'index/getDcInfo',
			header: that.header,
			method: 'POST',
			data: {
				dc_id: option.id
			},
			success: (result) => {
				// console.log(res)
				that.dc_info = result.data.data
				that.money = result.data.data.ya_money
				// 请求积分抵扣等
				uni.request({
					url: that.http + 'order/getIntegralMax',
					method: 'POST',
					header: that.header,
					data: {
						user_id: uni.getStorageSync('token')
					},
					success: (res) => {
						uni.hideLoading()
						that.account = res.data.data;
					},
					fail() {
						uni.showToast({
							title: '网络延迟，请刷新后重试!',
							icon: 'none'
						})
					}
				})
			},
			fail() {
				uni.showToast({
					title: '网络延迟，请刷新后重试!',
					icon: 'none'
				})
			}
		})
	},
	methods: {
		next() {
			var that = this;
			// console.log(this.status)
			// 判断是否为余额支付余额支付在此页面完成
			if (this.status == '0') {
				// 跳转到安全密码页面并传参
				var arr = {
					type: 1, //押金类型
					pay_type: 1, //余额支付
					money: this.money, //最后钱数
					integral: this.isAcc ? this.account.keyong : '', //抵扣积分书
					integral_moner: this.isAcc ? this.account.money : '' //积分抵扣金额
				}
				console.log()
				arr = JSON.stringify(arr)
				uni.navigateTo({
					url: '../pw/pw?arr='+arr
				})
			}
		},
		acc() {
			this.isAcc = !this.isAcc;
			if (this.isAcc) {
				this.money = Number(this.dc_info.ya_money) - Number(this.account.money)
				console.log(this.money)
			} else {
				this.money = this.dc_info.ya_money
			}
			console.log(this.isAcc)
		}
	}
}
